.container {
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding: 20px 0;
    width: 80%;
    margin: 0 auto;
}

.banner {
    width: 90%;
    max-width: 920px;
    height: auto;
    aspect-ratio: 4.6 / 1;
    margin: 0 auto;
    border-radius: 10px;
    border: 1px solid black;
    background: linear-gradient(90deg, #CFF37D 0%, #7DE88C 100%);
}

.showDangerousOnly {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 16px;
}

.distanceMode {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 16px;
}

.distanceChooser {
    background: none;
    border: none;
    color: #007bff;
    cursor: pointer;
    padding: 5px 10px;
    font-size: 16px;
    transition: all 0.3s ease;
}

.distanceChooser:hover {
    text-decoration: underline;
}

.distanceChooser.active {
    color: #333;
    text-decoration: underline;
    font-weight: bold;
}

.distanceChooser.inactive {
    text-decoration: underline;
}

.asteroidsList {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

@media (max-width: 1100px) {
    .container {
        width: 100%;
        padding: 0;
    }

    .banner {
        display: none;
    }

    .serviceText {
        width: 288px;
        height: 60px;
        position: absolute;
        top: 64px;
        left: 16px;
    }

    .navigationButtons {
        position: absolute;
        top: 124px;
        left: 16px;
    }

    .divider {
        width: 288px;
        height: 1px;
        position: absolute;
        top: 181px;
        left: 16px;
        background-color: #000;
    }

    .showDangerousOnly {
        position: absolute;
        top: 192px;
        left: 16px;
    }

    .distanceMode {
        position: absolute;
        top: 232px;
        left: 16px;
    }

    .asteroidsList {
        margin-top: 280px;
        padding: 0 16px;
    }
}
